import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ModalController } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';




/**
 * Generated class for the DetalleReservaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detalle-reserva',
  templateUrl: 'detalle-reserva.html',
})
export class DetalleReservaPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,public modalCtrl: ModalController,public apiProvider: ApiProvider, public loadingController:LoadingController, public events: Events) {

  this.dataCentro = {};
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetalleReservaPage');
    this.getDataCita(this.navParams.get('idCita'));
  }

  getEstado(idEstado){

  let estado='';

  idEstado == 1 ? estado = "Pendiente de confirmar" :
  idEstado == 5 ? estado = "Reprogramada" :
  idEstado == 2 ? estado = "Confimada" :
  idEstado == 3 ? estado = "Completada" :
  idEstado == 4 ? estado = "Cancelada" : estado =  "";

  return estado;

  }



  getDataCita(idCita){

  		let dataE = {idCita:idCita};
  		console.log(dataE);
        this.apiProvider.getDataCita(dataE)
      .then(data => {
       console.log(data);
              if(data){
        this.dataCita = data['cita'][0] || [];
         this.servicios = data['servicios'] || [];
       }
       else{console.log('Ha ocurrido un error');}

       
      });
  }

}



